<template>
  <div id="topbar">
    <div class="wrapper">
      <span class="logo">Resumer</span>
      <div class="actions">
        <a href="#" class="button primary" @click.prevent="signUpDialogVisible = true">注册</a>
        <MyDialog title="注册" :visible="signUpDialogVisible" @close="signUpDialogVisible = false">
          我就是slot内容
        </MyDialog>
        <a href="#" class="button">登录</a>
        <button  class="primary">保存</button>
        <button>预览</button>
      </div>
    </div>
  </div>
</template>

<script>
import MyDialog from './MyDialog'
export default {
  name: 'Topbar',
  data () {
    return {
      signUpDialogVisible: false
    }
  },
  components: {
    MyDialog
  }
}
</script>

<style scoped lang="scss">
#topbar{
	background: #fff;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.25);
  >.wrapper {
    min-width: 1024px;
    max-width: 1440px;
    margin: 0 auto;
    height: 64px;
  }
  >.wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 16px;
  }
  .logo {
  font-size: 24px;
  color: #000;
  }
}

.button {
  width: 72px;
  height: 32px;
  border: none;
  cursor: pointer;
  font-size: 18px;
  background: #ddd;
  color: #222;
  text-decoration: none;
  display: inline-flex;
  &:hover {
    box-shadow: 1px 1px 1px hsla(0, 0, 0, 0.5);
  }
  &.primary {
    background: #02af5f;
    color: white;
  }
}
</style>



